var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"container"},[_c('div',{staticClass:"header"},[_c('div',[_c('svg',{staticClass:"octicon octicon-list-unordered",attrs:{"aria-hidden":"true","height":"16","viewBox":"0 0 16 16","version":"1.1","width":"16","data-view-component":"true"}},[_c('path',{attrs:{"fill-rule":"evenodd","d":"M2 4a1 1 0 100-2 1 1 0 000 2zm3.75-1.5a.75.75 0 000 1.5h8.5a.75.75 0 000-1.5h-8.5zm0 5a.75.75 0 000 1.5h8.5a.75.75 0 000-1.5h-8.5zm0 5a.75.75 0 000 1.5h8.5a.75.75 0 000-1.5h-8.5zM3 8a1 1 0 11-2 0 1 1 0 012 0zm-1 6a1 1 0 100-2 1 1 0 000 2z"}})])]),_c('span',[_vm._v("奥陶纪大灭绝---死亡射线")])]),_c('div',{staticClass:"content"},[_vm._v(" 我是一条星甲鱼。我是在那场死亡射线暴之后存活了下来的后代，我们的祖先见证了这场劫后奇迹。 我们是生活在海底的杂食性鱼类，与其说是杂食性动物，不如说因为我们没有味觉，所以什么食物对于我们来说都没有关系，只要它能吃。我的好兄弟三叶虫，它也是个十足的吃货，甚至吃得比我还杂，我们有时会一起分享海藻大餐，也会逗一逗比我们小的生物。哦！说到体型，我们长得都比较小只，我包括我的同类只有十五厘米长，三叶虫大哥最多也只有70厘米，所以那些大家伙我们是吃不了了，只能吃吃他们的尸体，尸体我们也会把握时机吃刚挂掉的，别看我们不挑食，但我们打心里也是讲究人，哦不对，是讲究鱼，是讲究虫！ 我们一般生活在海底，生活起居基本无拘无束，啥都吃，但如果有敌人来了，我们也会钻进泥沙里，我们可是第一个有脊椎的动物嘞！我们可以扭动脊椎奋力俯冲，而三叶虫大哥也不怕，它有坚硬的外壳，身体一缩，或者躲进石头里就没事啦。 听我爷爷的爷爷的爷爷的爷爷的爷爷...说，从前我们生活的海域有两大恶霸种族，直壳鹦鹉螺和广翅鲎(hou4)，我的太太太太太太太太...爷爷，遇到他们就会往泥沙里钻。我们私底下都给他们俩取外号，直壳鹦鹉螺叫大螺，广翅鲎叫大翅。 他们真的太大啦，成年大螺一般在6-10米之间，而且他们的壳是圆锥形的，又有八只手，长得确实有点......见过戴小丑帽的章鱼吧？你知道的，八只手，尖帽子，游起来动力强阻力小，很难有生物能逃过大螺的追击，我的祖先们看到他们来了也会提早躲起来。至于大翅嘛，其实也没那么大，但也有2-4米长了，我们也很怕它，因为它有一对超大的钳子，游泳还贼快。被他的抓住了那可就有得玩了，有钳（钱）真好哇。 我和三叶虫大哥的祖先们倒是不怎么怕，我们长得小，他们很难抓住我们，反而还能在下午茶时间看到大螺和大翅的群殴。大螺是纯肉食性动物，大翅也是肉，所以大螺也吃大翅，但大翅也不是吃素的，它的钞能力（钳子）能够发起反击。经常就是你死我活的状态。 我们的祖先每天就这样生活着。 但是直到有一天，我们的祖先看到一些粉碎的直壳鹦鹉螺壳掉落到海底，按理来说这么坚硬的外壳不应该会被粉碎得这么零散，到底是发生了什么事呢？后来祖先们才知道，原来在我们生活的地球距离6000光年的地方，银河系的一颗新星爆炸了，变成了超新星，它的两极发出巨大的电子能量束，以光的速度向外发射出去，直到我们所在的蓝色小星球------地球。 那是多么微小的概率啊！但它真的发生了。 【需要介绍这场伽马射线暴了】 当这个光束到达地球时，首先撕裂了大气层，十秒钟之后，这场光电秀变成了诡异的平静。对，这束光的长度也仅有十秒的光年长度，但它的能量足以摧毁我们的大气层。在这之后，天空多了一颗和太阳一样闪耀的光球，这是超新星燃烧的余烬，而这个光球足足持续了好几个月。 没有了大气层，紫外线可以源源不断地到达地表，阳光比撒哈拉沙漠还要强50倍。浅海域的生物沦陷了，浮游生物和珊瑚礁相继死去，食物链的基层遭到了破坏！食物越来越少，生活在浅海域的大螺纷纷往深水区迁移，但你知道的，海底越深，水压越大，于是我们祖先就看到了那些掉落的大螺的壳。 啊......曾经的一代霸主，竟然沦落到这等地步。而大翅就稍微幸运一点，他们比大螺小一倍，吃得也是大螺的一半，所以食物还能满足，直壳鹦鹉螺有个远方亲戚，叫卷壳鹦鹉螺，它们体型和我们一般大小，大翅打不过大螺，不过欺负欺负卷壳鹦鹉螺还是可以滴，他们就这样活了下来。 但这道死亡射线对我们生活的地球造成的影响还远远没有结束。击穿的大气层实际上是空气分子被击穿了，分子重组成了烟雾弥漫在空中，这些烟雾越来越多，越来越多，最终将笼罩了整个天空，太阳光进不来，地球的温度下降到很低，这样的气候变化造成了可怕的洋流，而洋流又会促使气候朝着更恶劣的方向发展.......就这样很多浅海域的动物都跑到深海域，但面临的是强大的水压。 但这层烟雾带来的影响还没结束，在很长的一段时间里，温度下降导致地球越来越寒冷，冰层甚至厚达15千米，直接让海平面下降了六十米，浅海床裸漏出来，生活在那里的生物纷纷搁浅了，比如不能移动的珊瑚礁，不愿下沉的幼年大螺，场面尸横遍野。 这个冰川时代，影响了70万年。 之后230万年里，地球才慢慢从冬眠中苏醒，但70%的生物都灭绝了，在这之后，地球才恢复到原来的热带水平。也就是我现在所生活的时代，而这个时代大螺不在了，大翅寥寥无几了，很多那时的大型生物都不在了，但我们，星甲鱼！靠着纯粹地杂食存活了下来，甚至为了躲过这场浩劫，我们还繁衍出了旁系后代，后人称他们为棘鱼，他们进化出了下颌，这是非常重要的器官！我们星甲鱼是无颌鱼类，吃东西不叫吃，叫吸，而棘鱼可以靠它的下颌主动捕食，咀嚼食物，大大提高了捕食和消化能力，可以说很棒了。 关于我的祖先们生活的奥陶纪的事情，就介绍到这里啦。 ")])])
